import React from "react";
import classNames from "classnames";

export default function Map(props) {
	let tiles = props.tiles,
	mapTiles = [];

	tiles.forEach((tileColumn, i) => {
		tileColumn.forEach((tile, j) => {
			const typeClass = 'cartographers__tile--' + tile.type,
				uniqueKey = i + '-' + j,
				tileClassName = classNames(
					"cartographers__tile",
					typeClass
				);

				mapTiles.push(
				<div
					className={tileClassName}
					key={uniqueKey.toString()}
				>
					{tile.type === 'mountain' ? 
						<img alt="mountain tile" src="data:image/png;base64,"/> 
						: tile.type === 'ruin' ? 
						<img alt="ruin tile" src="data:image/png;base64,"/>
						: ''
					}
				</div>
			);
		});
	});

	return (
		<div className="cartographers__map">
			{mapTiles}
		</div>
	);
};